
.Bar
{
    background-color: #1a1b1d;
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding-inline: 1%;
}
.navigation
{
    justify-content: end;  
}

.navigationCell
{
    white-space: nowrap;
    margin: 5%;
    max-width: fit-content;
}
.navigationCell a
{
    justify-content: end;
    text-align: center;
    font-size: 20px;
    color: aliceblue;
    text-decoration: none;
}